import React from 'react';
import { Transition } from 'react-transition-group';
import { reflow } from 'utils/transition';
import Section from 'components/Section';
import images from 'utils/images';
import { media } from 'utils/style';
import { useWindowSize } from 'hooks';
import { Grid } from '@material-ui/core';

const Skills = ({ id, visible, sectionRef }) => {
  const titleId = `${id}-title`;
  const { width } = useWindowSize();
  const isMobile = width <= media.tablet;
  return (
    <Section
      className="awards"
      as="section"
      id={id}
      ref={sectionRef}
      aria-labelledby={titleId}
      tabIndex={-1}
    >
      {' '}
      <Transition in={visible} timeout={0} onEnter={reflow}>
        <Grid container>
          <Grid item xs={12} className="center">
            <h4 className="heading--level-3 title">Tech I'm familiar with</h4>
          </Grid>
          {isMobile ? (
            <Grid container className="skillContainer">
              {images.map(images => (
                <Grid item xs={3} className="skillItem">
                  <img src={images} alt={'skills'} className="skillImage" />
                </Grid>
              ))}
            </Grid>
          ) : (
            <canvas id="canvasForSkills"></canvas>
          )}
        </Grid>
      </Transition>
    </Section>
  );
};
export default Skills;
